html,
body,
#root {
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0;
    -ms-flex-align: center;
        align-items: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;
}

.btn {
    background: #a3c3ff;
    border: 2px solid rgb(69, 121, 218);
    padding: 10px;
    margin: 10px;
    cursor: pointer;
}

.win {
    background: lightgreen;
    color: creen;
    border: 4px solid green;
}

.board {
    border-collapse: collapse;
    border: 3px solid #000;
    width: 100%;
    height: 100%;
}
.board-body {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
}

.board-row__element {
    width: 40px;
    height: 40px;
}


